.userfocus{
  padding-top: 0px;
}

.userfocus .content{
  margin-top: 60px;
  padding: 0px 5% 55px 5%;
  background-color: #fff;
  border-radius: 2px;
  -webkit-box-shadow: 0 1px 2px 0 rgba(0,0,0,.05);
          box-shadow: 0 1px 2px 0 rgba(0,0,0,.05);
  width: 70%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
}
.userfocus .content .btn-1{
  margin-left:-50px;
  margin-top:0px;
  width:150px;
  height:30px;
  border:none;
}
.userfocus .content .btn-1:hover{
  background-color: #AAAAAF;
}
.userfocus .content .btn-2{
  margin-left:0px;
  marginTop:0px;
  width:150px;
  height:30px;
  border:none;
}
.userfocus .content .btn-2:hover{
  background-color: #AAAAAF;
}
.userfocus .content .cell {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  margin-top: 35px;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.userfocus .right{
  display: inline-block;  
  position: fixed;
width: 300px;
  margin-top: 60px;
}

.userfocus .right .card {
  margin-left: 10px;
  -webkit-box-shadow: 0 1px 3px rgba(0,0,0,.1);
          box-shadow: 0 1px 3px rgba(0,0,0,.1);
  background: #fff;
  padding: 10px;
  color: #8590a6;
  font-size: 14px;
  line-height: 25px;
}

.userfocus .footer{
  margin-left: 13px;
  margin-top: 5px;  
}

.userfocus .footer .footer-dot{
  font-size: 13px;
  line-height: 2;
    color: #8590a6;
}

.userfocus .footer .footer-dot:after {
  margin: 0 5px;
  content: "\B7";
}

.userfocus .footer .footer-item{
  text-decoration: none;
  display: inline-block;
  font-size: 13px;
  color: #8590a6;
}

.userfocus .moblie {
  display: none;
}
@media (max-width: 1024px) {
  .userfocus{
    padding-top: 135px;
  }
  .userfocus .left{
    width: 100%;
  }
  .userfocus .right{
    display: none;
  }

  .userfocus .moblie {
    display: block;
  }
}


